body{
    background-color: black;
}

    input {
        font-size: 20px;
        padding: 10px 20px;
        min-width: 500px;
        margin: 30px auto;
        display: block;
        text-align: center;  
        color:black;
      }

 .movieCard{
    display: grid;
   grid-template-columns: auto auto auto auto auto;
  grid-gap: 15px;  
  
  /* height:150px; */
  
}
li{
    position: relative;
    border-radius: 10px;
    /* box-shadow: 5px 5px grey  ; */
    height:456px;
    width:250px
}
img{
    width:100%;
    height:300px;
    border-radius: 10px;
    
}
.title{
    color:rgba(255, 255, 255, 0.685);
    font-size: 22px;
    position: absolute;
    top :250px;
    left:0px;
    display: none;
    text-align: center;
    padding: 30px;
    /* box-shadow: 5px 5px rgba(255, 255, 255, 0.685) */
}
li:hover .title{
display: block; 
/* box-shadow: 5px 5px rgba(255, 255, 255, 0.685) */

 }  
li:hover{
    box-shadow: 5px 5px rgba(255, 255, 255, 0.685)  
}
input.error {
    border: 2px solid red;
    color: red;
  }

.display{
    display:block;
}